.titleHome{
    font-size: 2.2rem;
    color: #2b7ccd;
} 

.NameHome{
    font-size: 2.2rem;
}

.containerInfoName{
    display: flex;
    flex-direction: column;
    width: 52.93%;
    text-align: center;
}

.HomeStyle{
    display: flex;
    flex-direction: row;
    color: #2e3842;
    background:#c8ddf2;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    flex-wrap: wrap;
    -webkit-user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -ms-user-select:none;
    
}

.profilePictureStyle{
    border-radius: 10px;
    height: 550px;
    opacity: 1;
}

.centradoHome{
    min-height: 90vh;
    justify-content: center;
    align-content: center;
    display: flex;
    flex-direction: column;
}

@media screen and (max-width: 900px) {
    .NameHome{
        font-size: 1.4rem;
    }
    .titleHome{
        font-size: 1.4rem;
    }
    .profilePictureStyle{
        border-radius: 10px;
        height: 300px;
    }
    .centradoHome{
        margin: 5px;
    }
}

@media screen and (max-width: 1100px) {
    .NameHome{
        font-size: 2rem;
    }
    .titleHome{
        font-size: 2rem;
    }
    .profilePictureStyle{
        border-radius: 10px;
        height: 500px;
    }
    .centradoHome{
        margin: 5px;
    }
    .containerInfoName{
        width: 90%;
    }
}